@import '~theme/variables.scss';

.navigation {
  position: fixed;
  top: 0;
  left: 0;
  height: $navbar-height;
  width: 100%;

  .img {
    max-height: none;
  }

  .burgerIcon {
    margin-left: 0;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
  }

  .navbarLogo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}

.navbarBrand {
  width: 250px;
  background-color: $blue;
  position: relative;
}